@font-face {
  font-family: "SF Pro Display";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("SFProDisplay-Regular"), url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff"); }

@font-face {
  font-family: "SF Pro Display";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("SFProDisplay-Medium"), url("../fonts/SFProDisplay-Medium.woff2") format("woff2"), url("../fonts/SFProDisplay-Medium.woff") format("woff"); }

@font-face {
  font-family: "SF Pro Display";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("SFProDisplay-Semibold"), url("../fonts/SFProDisplay-Semibold.woff2") format("woff2"), url("../fonts/SFProDisplay-Semibold.woff") format("woff"); }

@font-face {
  font-family: "SF Pro Display";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("SFProDisplay-Bold"), url("../fonts/SFProDisplay-Bold.woff2") format("woff2"), url("../fonts/SFProDisplay-Bold.woff") format("woff"); }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.page-body {
  background-color: #ffffff;
  color: #3c3b3b;
  font-family: "SF Pro Display";
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  line-height: 30px; }

.page-header__wrapper {
  background-color: #262626;
  padding-top: 12px;
  padding-bottom: 12px; }

.page-header__top {
  margin: 0 auto;
  width: 1170px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.page-header__contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column; }

.page-header__contacts a {
  color: #ffffff;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
  position: relative; }

.page-header__contacts i {
  font-size: 14px;
  line-height: 21px;
  color: #ffffff; }

.page-header__contacts--tel {
  margin-bottom: 17px; }

.page-header__contacts--tel::before {
  content: "";
  position: absolute;
  width: 28px;
  height: 28px;
  left: -40px;
  top: 0;
  background-image: url("../img/phone_icon.png"); }

.page-header__contacts--mail::before {
  content: "";
  position: absolute;
  width: 28px;
  height: 28px;
  left: -40px;
  top: 0;
  background-image: url("../img/mail-icon.png"); }

.page-header__account {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 307px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.page-header__account--button {
  color: #ffffff;
  background-color: #67a8d2;
  padding: 7px 42px;
  font-size: 20px;
  line-height: 30px;
  display: block;
  height: 44px;
  width: 232px; }

.page-header__account--icon {
  width: 34px;
  height: 34px;
  background-color: #67a8d2;
  border-radius: 50%;
  position: relative; }

.page-header__account--icon::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 34px;
  left: 0;
  top: 0;
  background-image: url("../img/account-icon.png");
  background-repeat: no-repeat; }

.page-nav__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1170px;
  margin: 0 auto;
  list-style: none;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0;
  padding-top: 16px; }

.page-nav__item {
  font-size: 20px;
  line-height: 30px;
  color: #262626;
  text-transform: uppercase;
  padding: 0;
  position: relative; }

.page-nav__item::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 20px;
  right: -65px;
  top: 5px;
  background-color: rgba(38, 38, 38, 0.2); }

.page-nav__item:last-child::after {
  display: none; }

.page-nav__item:last-child {
  padding-right: 20px; }

.page-nav__item:last-child::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 8px;
  right: 0;
  top: 10px;
  background-image: url("../img/arrow-down.png");
  background-repeat: no-repeat; }
